import { FC } from "react";
import { ErrorMessage, Field, Form, Formik } from "formik";
import styles from "./Registration.module.scss";
import * as Yup from 'yup';
import classNames from "classnames";
import { fetchRegister } from "../../../redux/auth/asyncActions";
import { useAppDispatch } from "../../../redux/hooks";
import { useNavigate } from "react-router-dom";

interface IProps {
  onClose: (e: any) => any;
}

export const Registration: FC<IProps> = ({ onClose }) => {
  const dispatch = useAppDispatch();
  const navigate = useNavigate();

  return (
    <div className={styles.form}>
      <Formik
        initialValues={{ 
          fullName: '',
          email: '', 
          password: '',
          avatarUrl: '',
        }}
        validationSchema={Yup.object({
          fullName: Yup.string().min(3, ' 3 ').required(' '),
          email: Yup.string().email(' email').required(' '),
          password: Yup.string().min(5, ' 5 ').required(' '),
          avatarUrl: Yup.string().url(' URL').min(20, ' 20 '),
        })}
        onSubmit={async (values, { setSubmitting }) => {
          const data: any = await dispatch(fetchRegister(values));

          if (!data.payload) {
            return alert('  ');
          }
          if ('token' in data.payload) {
            window.localStorage.setItem('token', data.payload.token);
          }

          onClose(false);
          navigate('/account');
          setSubmitting(false);
        }}
      >
        {({ isSubmitting, touched, errors }) => (
          <Form>
            <label htmlFor="fullName"> </label>
            <Field 
              id="fullName" 
              name="fullName" 
              type="text" 
              placeholder=" "
              className={classNames(
                { [styles.borderRed]: touched.email && errors.email }
              )}
            />
            <ErrorMessage name="fullName" component="div" />
            <label htmlFor="email">Email</label>
            <Field 
              id="email" 
              name="email" 
              type="email" 
              placeholder="test@test.com"
              className={classNames(
                { [styles.borderRed]: touched.email && errors.email }
              )}
            />
            <ErrorMessage name="email" component="div" />
            <label htmlFor="password"></label>
            <Field 
              id="password"
              type="password" 
              name="password" 
              className={classNames(
                { [styles.borderRed]: touched.password && errors.password }
              )}
            />
            <ErrorMessage name="password" component="div" />
            <label htmlFor="avatarUrl"></label>
            <Field 
              id="avatarUrl" 
              name="avatarUrl" 
              type="text" 
              placeholder="https://test.com/image123.jpg"
              className={classNames(
                { [styles.borderRed]: touched.email && errors.email }
              )}
            />
            <ErrorMessage name="avatarUrl" component="div" />
            <button type="submit" disabled={isSubmitting}>
              
            </button>
          </Form>
        )}
      </Formik>
    </div>
  );
};
